import React, { useCallback, useEffect, useState } from 'react';

// COMPONENTS
import SearchBar from '../../components/UI/SearchBar/SearchBar';
import Panel from '../../components/UI/Panel/Panel';
import Item from '../../components/UI/Item/Item';

// STYLES
import styles from './Users.module.css';

// FIREBASE
import { fs } from '../../firebase/config';

// REDUX
import { useSelector } from 'react-redux';

// MODELS
import User from '../../models/user';

// EXTERNAL
import InfiniteScroll from 'react-infinite-scroller';


const labels = ["Email", "Phone", "ID"]

const Users = (props) => {
    const uid = useSelector(state => state.auth.uid)
    const [users, setUsers] = useState([])
    const [lastVisible, setLastVisible] = useState(null)

    // fetch all checkins
    const getUsers = useCallback(() => {
        const query = fs.collection(`organizations/${uid}/users`).where("name", ">=", "A").limit(25)

        query.get().then((querySnapshot) => {
            const users = [];
            querySnapshot.forEach((doc) => {
                users.push(
                    new User(
                        doc.data().name,
                        doc.data().email,
                        doc.data().phoneNumber,
                        null,
                        doc.data().userId
                    )
                )
            })
            setUsers(users)
            if (querySnapshot.size < 25) {
                setLastVisible(null)
            } else {
                setLastVisible(querySnapshot.docs[querySnapshot.size - 1])
            }
        })
    }, [uid])

    const loadMore = () => {
        if (lastVisible) {
            const query = fs.collection(`organizations/${uid}/users`).startAfter(lastVisible).limit(25)
            query.get().then((querySnapshot) => {
                const newUsers = users
                querySnapshot.forEach((doc) => {
                    newUsers.push(
                        new User(
                            doc.data().name,
                            doc.data().email,
                            doc.data().phoneNumber,

                        )
                    )
                })
                setUsers(newUsers)
                if (querySnapshot.size < 25) {
                    setLastVisible(null)
                } else {
                    setLastVisible(querySnapshot.docs[querySnapshot.size - 1])
                }

            })
        }
    }


    useEffect(() => {
        getUsers()
    }, [getUsers])

    const onClickHandler = (id) => {

        props.history.push(`users/${id}`)
    }

    return (
        <div>
            <div className={styles.SearchBarContainer}>
                <SearchBar />
            </div>
            <Panel onClick={onClickHandler} title={"Users"} labels={labels}>
                <InfiniteScroll
                    pageStart={0}
                    loadMore={loadMore}
                    hasMore={true || false}
                    useWindow={true}
                    threshold={0}
                >
                    {users.map(user => {
                        const itemArr = [user.email, user.phoneNumber, user.id]
                        return (
                            <Item key={Math.random()} title={user.name} itemArr={itemArr} onClick={() => onClickHandler(user.id)} />
                        )
                    })}
                </InfiniteScroll>
            </Panel>
        </div>
    )
}

export default Users;